@import "src/variables/variables";

.list {
  margin-bottom: 140px ;
 @media(max-width: 420px) {
   width: 400px;
   overflow-x: scroll;
   overflow-y: hidden ;
 }
}
.title {
  font-size: 30px;
  margin-top: 60px;
  margin-left: 20px;
  font-weight: 400;

}
.goodsPropositions {
  display: grid;
  margin-top: 20px;

  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
 @media (max-width: 1300px) {
   grid-template-columns: 1fr  1fr 1fr 1fr;
 }
  @media (max-width: 1100px)  {
    grid-template-columns: 1fr  1fr 1fr 1fr 1fr ;
  }
  .goodsItem {
    text-decoration: none;
    align-self:center;

    .wrap {


      padding: 20px 24px;
      max-width: 240px;
      border-radius: 4px;
      &:hover {
        box-shadow: 0 2px 14px rgba(0,0,0,.2);
      }
      .separate {
        display: flex;
        align-items: center;

        position: absolute;


        .stickerNew {
          font-family: 'PT Root UI';
          font-size: 11px;
          font-weight: 500;
          background: #9bcd57;
          color: #fff;
          padding: 6px 10px;
          border-radius: 3px;




        }
        .stickerSale   {
          font-size: 11px;
          font-family: 'PT Root UI';
          background:coral;
          color: #fff;
          padding: 4px;
          margin-right: 20px;


          border-radius: 4px;
          font-weight: 500;
        }
        .stickerHit   {
          font-family: 'PT Root UI';
          font-size: 11px;
          background: tomato;
          color: #fff;
          padding: 4px 14px;
          border-radius: 4px;
          font-weight: 500;



        }
        .rating {
          display: none;
        }


      }
      a {
        text-decoration: none;
      }
      .image {
        width: 160px;
        height: 160px;
        align-items: center;
        display: flex;
        justify-content: center;

        margin: 10px auto;
        text-decoration: none;
        img {
          width: 90%;

        }
      }
      .spanWrap {
        display: flex;
        flex-direction: column;
        text-decoration: none;
        .spanTitle {
          display: block;
          color: #000;
          height: 60px;
          text-decoration: none;

        }

      }


      &:hover {
        .btn {
          visibility: visible;
          opacity: 1;
          bottom: 0;
        }
      }
    }
  }
}
.bottom {
  display: flex;
  align-items: center;
  .price {
    font-family: 'PT Root UI';
    margin-top: 30px;
    color: #000000;
    font-size: 2rem;
    font-weight: 300;
    flex-wrap: nowrap;
    width: 200px;
    display: block;
  }
  .btn {
    border: none;
    background: #cba2dd;
    color: #fff;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    opacity: 0;
    position: relative;
    bottom: -3px;
    border-radius: 50%;
    padding: 5px;
    margin-top: 10px;
    cursor: pointer;
    box-shadow: 0 2px 4px rgba(0,0,0,.2);
    &:hover {
      background: #5e2d79;
    }
    svg {
      fill: #fff;

    }

  }
}
